import React, {useState, useEffect} from "react";
import Axios from "axios";
import Movie from "../components/Movie";
import spinImg from '../download.png';
import "./Home.css";

function useFetch(url) {
    const [movies, setMovies] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("");

    const callUrl = async () =>{
        try {
            const {data: {data: { movies }}} = await Axios.get(url);
            setMovies(movies);
        } catch {
            setError("ㅠㅠ");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() =>{
        callUrl();
    }, []);

    return {movies, loading, error};
}

function Home() {
    const {movies, loading, error} = useFetch("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    return (
        <section className="container">
            {loading ? (
                <div className="loader">
                    <img src={spinImg} className="spin" />
                    <div className="loader__text">Loading...</div>
                </div>
            ) : (
                <div className="movies">
                    {movies.map(movie => (
                        <Movie
                            key={movie.id}
                            id={movie.id}
                            year={movie.year}
                            title={movie.title}
                            summary={movie.summary}
                            poster={movie.large_cover_image}
                            bg_img={movie.background_image_original}
                            genres={movie.genres}
                        />
                    ))}
                </div>
            )}
        </section>
    );
}

export default Home;
